aper provides a contribution to the field of AIED, one that we can all learn from– if so, become a champion for the paper by providing explicit suggestions to the authors on how to improve the paper. Offer constructive suggestions; include questions on aspects that were not clear and why they weren’t clear so the authors know what they need to revise. ")]),_c('p',[_vm._v(" Likewise, stating that a paper should be rejected because it (has limited sample size/ it does not make a contribution / it should have used analysis X instead of Y/etc) is not sufficient – unpack the details. If another analysis method should have been used, make it clear why the results are not valid with the present one. If the sample size should have been bigger, consider if the paper could still offer a contribution because it presents a novel insight on some feature (e.g., instructional design). While papers that do not provide a contribution should not be accepted, do not set out to reject a paper. ")]),_c('p',[_vm._v(" When writing a review, keep a number of questions in mind: (1) what was the goal of the work? (2) was it well motivated / does the literature review make a gap clear that needs to be filled? (3) does the work address the research goals? (4) are the analyses used appropriate and valid? (4) are the conclusions valid? ")]),_c('p',[_vm._v(" If they are not key resources missing, please avoid self-promotion through suggestions of your own papers to be cited. ")])])])])])}]

